@media only screen and (max-width: 1020px) {
  .profile-box {
    width: 280px;
    height: 280px;
  }
}

@media only screen and (max-width: 760px) {
  .profile-container {
    background-color: #09bfad;
    max-height: 300px;
  }
  .robocoin {
    justify-content: flex-end;
  }
  .profile {
    padding-top: 0%;
  }
  .profile-box {
    width: 280px;
    height: 280px;
    align-items: center;
    z-index: 1;
    margin-top: 60px;
  }
  .user-info {
    background-color: #fff;
    color: black;
  }
  .username {
    text-align: center;
  }
  .about {
    text-align: left;
    width: 320px;
  }
  .about {
    width: 450px;
  }
}

@media only screen and (max-width: 400px) {
  .robocoin {
    justify-content: center;
  }
}
